
.TeamScoreBoard{
    width: 100%;
    height: 100px;
    background-color: grey;
    margin-top: 10px;
}

.BlueTeamScoreBoard{
    width:45%;
    height:100%;
    background-color:rgba(46, 129, 253, 0.911);
    float: left;
    
}

.TeamScoreBoard table{
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 100%;
}


.Live{
    width:10%;
    height:100%;
    background-color: green;
    float: left;
}

.RedTeamScoreBoard{
    width:45%;
    height:100%;
    float:left;
    background-color:rgb(248, 70, 70);

}

.vertical-center {
    margin: 0;
    position: relative;
    top: 50%;
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
}


.beautiful-text{
    font-family: "Didot", "Courier New", monospace;
    font-size: 28px;
    font-weight: bold;
    color: black
}

    

