.PlayerScoreBoard{
    width: 100%;
    background-color: white;
    margin-top: 10px;
}

.UniquePlayerScoreBoard td,th{
    text-align: center;
    border-top: 1px grey solid;
    font-weight: bold;

}

.UniquePlayerScoreBoard th{
    font-size: 10px;

}

.UniquePlayerScoreBoard table td{
    font-size: 14px;
}

.UniquePlayerScoreBoard table {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 100%;
    border: 1px solid;
}

.UniquePlayerScoreBoard{
    width:45%;
    height:100%;
    background-color: lightgrey;
    float: left;
}

#redTeam{
    transform: translateX(20%)
}

progress[value] {
    /* Reset the default appearance */
    -webkit-appearance: none;
    appearance: none;
    width: 100px;
    height: 20px;
        /* Set the progressbar to relative */
    position:relative;
    text-align: center;
    color: black;
}


progress:before {
    content: attr(data-label);
    font-size: 0.8em;
    vertical-align: 0;
    
    /*Position text over the progress bar */
    position:absolute;
    left:0;
    right:0;
}

progress::-webkit-progress-value {
	background: green;
	transition: width 1s ease-in-out;
}

@media only screen and (max-width: 900px) {
    progress[value] {
        /* Reset the default appearance */
        -webkit-appearance: none;
        appearance: none;
        width: 100%;
        height: 20px;
    }
    #redTeam{
        transform: translateX(10%)
    }

    img{
        width:20px
    }

    .UniquePlayerScoreBoard table td,th{
        font-size: 14px;
    }
}
